body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Nav-Element {
  width: 130px;
  padding: 15px;
  text-decoration: none;
  color: rgb(255, 255, 255);
  border-radius: 3px;
  box-sizing: border-box;
  position: relative;
  border-left: 1px solid rgba(131, 131, 131, 0.74);
}

.Nav-Element:last-child{
  border-right: 1px solid rgba(131, 131, 131, 0.74);
}

.Nav-Element:hover {
  color: #e65c00;
}

.NumberOfPurchases{
  color:rgb(255, 0, 0);
  background-color: rgb(255, 202, 87);
  border-radius: 10px;
  padding: 0px 8px;
  border: 2px solid #fff;
  font-size: .8em;
  position: absolute;
  top: 5px;
  right: 35px;
}


.Nav-Element.active {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  color: #e65c00;
}

.Nav-Element.active svg{
  fill: #e65c00;
}


@media(max-width:800px){
  .Nav-Element{
    width: 90px;
  }

  .NumberOfPurchases{
  font-size: .7em;
  right: 20px;
  }
}
.SideToggleButton{
    width: 30px;
    height: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    cursor: pointer;
    box-sizing: border-box;
    margin: auto auto auto 0;
}

.Button-Line{
    width: 30px;
    height: 2px;
   border-bottom: 2px solid #fff;
}

@media(min-width:905px){
    .SideToggleButton{
        display: none;
    }
}
@media(max-width:340px){
    .SideToggleButton{
        margin: auto 4px;
    }
}
.Nav {
  display: flex;
  position: fixed;
  z-index: 100;
  top:0;
  left: 0;
  width: 100%;
  justify-content: flex-end;
  box-sizing: border-box;
  text-transform: uppercase;
  font-weight: 700;
  font-size: .9em;
  background-color: rgba(163, 163, 163, 0.911);
  padding: 0 30px;
  text-align: center;
}

.IconLink{
  width: 25px;
  height: 20px;
  margin: 0 auto;
  fill:#fff;
}

.IconLink:hover {
  fill: #e65c00;
}

@media(max-width:900px){
  .Nav{
  padding: 0 5px;
  justify-content: center;    
  }
}
.Input,
.Textarea {
  margin: 10px;
  padding: 8px;
  font-size: 1.1em;
  width: 90%;
  margin: 10px auto;
  font-weight: 600;
  display: block;
  box-sizing: border-box;
}

.Input {
  text-align: center;
}

.Input:focus,
.Textarea:focus, .Select:focus {
  border: 1px solid #555;
  outline-width:0;
}
.Label {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1.1em;
}

.Textarea {
  resize: none;
  height: 15vh;
}

.Text {
  font-size: 1.2em;
  color: #555;
}

.Select{
  padding: 3px;
}
.ImageContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 30vw;
}

.FrontImage {
  width: 20vw;
  /* height: 30vh; */
  display: flex;
  flex-basis: 80%;
  margin: 20px auto;
}

.EachImageContainer {
  display: flex;
  flex-direction: column;
  width: 9vw;
  /* height: 8vh; */
  margin: 12px 1%;
  position: relative;
}

.AllImages {
  width: 10vw;
}

.AllImages:hover {
  opacity: 0.2;
  cursor: pointer;
}

.EachImageContainer:hover.EachImageContainer::after {
  content: "Select";
  pointer-events: fill;
  cursor: pointer;
  font-size: 1.5em;
  font-weight: bold;
  color: rgb(58, 58, 58);
  position: absolute;
  left: 55%;
  top: 30%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 1;
  pointer-events: none;
}
.EachImageContainer:hover .AllImages {
  opacity: 0.2;
}

.RemoveItem {
  cursor: pointer;
  font-size: 1.2em;
  font-weight: bold;
  position: absolute;
  right: 0;
  top: -30%;
  color: rgba(255, 0, 0, 0.836);
  opacity: 1;
}

.NoImg {
  height: 30vh;
  width: 20vw;
  box-shadow: inset 0px 0px 20px 0px rgba(97, 97, 97, 1);
  display: flex;
  margin: 20px auto;
  border: 1px solid #999;
}

.NoImg p {
  margin: auto;
  font-weight: 600;
  font-size: 1.3em;
}

.Button {
  box-sizing: border-box;
  padding: 8px;
  margin-bottom: 10px;
  border-radius: 3px;
  background-color: rgb(206, 206, 206);
  border: none;
  font-weight: 600;
  font-size: 1em;
  color: aliceblue;
  width: 100%;
}

.Button.Confirm{
  background-color: rgba(25, 187, 66, 0.933);
}

.Button.Success {
  background-color: rgb(67, 148, 42);
}

.Button.Change{
  background-color: rgba(255, 0, 0, 0.589);
  font-weight: 600;
  font-size: 1em;
}
.Button.Danger {
  background-color: rgb(255, 0, 0);
  
}

.Button.Order {
  background-color: rgb(255, 230, 86);
  color: rgb(0, 0, 0);

}
.Button.Button.Order:hover {
  background-color: rgb(231, 197, 0);
}
.Button:hover{
 -webkit-transform: scale(1.1);
         transform: scale(1.1);
 -webkit-transition: 0.2s ease;
 transition: 0.2s ease
}
.Button:hover {
  cursor: pointer;
}

.Button:focus {
  outline: none;
}

.Remove{
  background-color: rgb(255, 0, 0);
}


